import {useState, useEffect} from 'react'
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import ListItemAvatar from '@mui/material/ListItemAvatar';
import Avatar from '@mui/material/Avatar';
import ImageIcon from '@mui/icons-material/Image';

import {Paper, Stack, Typography, IconButton, InputBase, Box, Slider,Grid, Tabs, Tab} from '@mui/material'

import SortIcon from '@mui/icons-material/Sort';
import SearchIcon from '@mui/icons-material/Search';
import RiceBowlIcon from '@mui/icons-material/RiceBowl';
import SetMealIcon from '@mui/icons-material/SetMeal';

import { grey, pink } from '@mui/material/colors';


import data from './yoshinoya.json'

for(var food of data){
  var p = Math.round(food[4] * 4 / food[3] * 100);
  var f = Math.round(food[5] * 9 / food[3] * 100);
  var c = Math.round(food[6] * 4 / food[3] * 100);
  food.push(p, f, c);
}

const categories = ["全て", "丼", "定食", "カレー"];

const sortOptions = [
  "デフォルト",
  "カロリー多い順",
  "カロリー少ない順",
  "たんぱく質多い順",
  "たんぱく質少ない順",
  "脂質多い順",
  "脂質少ない順",
  "炭水化物多い順",
  "炭水化物少ない順",
]

const icons = [
  <SortIcon />,
  <SearchIcon />,
  <RiceBowlIcon />,
  <SetMealIcon />,
]

export default function MyList() {
  const [foods, setFoods] = useState(data);
  const [text, setText] = useState("");
  const [categoryId, setCategoryId] = useState(0);
  const [sortId, setSortId] = useState(0);

  useEffect(() => {
    const foods = data.filter((food) => {
      var ok = food[0].includes(text);
      if (categoryId != 0) ok = ok && food[0].includes(categories[categoryId]);
      return ok;
    });

    if(sortId == 0) ;
    else if(sortId == 1) foods.sort((a, b) => b[3] - a[3]);
    else if(sortId == 2) foods.sort((a, b) => a[3] - b[3]);
    else if(sortId == 3) foods.sort((a, b) => b[4] - a[4]);
    else if(sortId == 4) foods.sort((a, b) => a[4] - b[4]);
    else if(sortId == 5) foods.sort((a, b) => b[5] - a[5]);
    else if(sortId == 6) foods.sort((a, b) => a[5] - b[5]);
    else if(sortId == 7) foods.sort((a, b) => b[6] - a[6]);
    else if(sortId == 8) foods.sort((a, b) => a[6] - b[6]);

    setFoods(foods);

  }, [categoryId, sortId, text])



  return (
    <Stack spacing={2} sx={{p: 2, boxSizing: 'border-box'}}>
      <Stack direction="row"justifyContent="space-between">
        <Typography fontSize={30}>
          <b>吉野家</b>
        </Typography>
        <SortButton setSortId={setSortId}/>
      </Stack>
      <MyInput setFoods={setFoods} text={text} setText={setText}/>
      <MyTabs value={categoryId} setValue={setCategoryId}/>
      <List sx={{ width: '100%', maxWidth: 360, bgcolor: 'background.paper' }}>
        {foods.map((food, i) => {
          return(
            <ListItem disablePadding>
              <ListItemAvatar>
                <Avatar sx={{bgcolor: pink[200]}}>
                  {icons[i % 4]}
                </Avatar>
              </ListItemAvatar>
              <ListItemText
                primary={`${food[0]} ${food[1] ?? ""}`}
                secondary={`${food[3]}kcal - ${food[4]}g ${food[5]}g ${food[6]}g`}
              />
            </ListItem>
          )
        })}
      </List>
    </Stack>
  );
}

function MyTabs(props) {
  return (
    <Box sx={{ width: "100%", bgcolor: 'background.paper' }}>
      <Tabs
        value={props.value}
        onChange={(event, newValue) => props.setValue(newValue)}
        variant="scrollable"
        scrollButtons={false}
        aria-label="scrollable prevent tabs example"
      >
        {categories.map((category) => (
          <Tab label={category} />
        ))}
      </Tabs>
    </Box>
  );
}

function SortButton(props) {
  return (
    <Paper
      component='select'
      onChange={(event) => props.setSortId(event.target.value)}
      elevation={0}
      sx={{color: pink[200],outline: 'none', border: 'none',fontSize: 15}}
    >
      {sortOptions.map((option, i) => (
        <option value={i}>{option}</option>
      ))}
    </Paper>
  )
}

function MyInput(props) {
  return (
    <Paper
      component="form"
      elevation={0}
      sx={{ borderRadius: 2, display: 'flex', alignItems: 'center', width: '100%', bgcolor: grey[200] }}
      onSubmit={(e) => e.preventDefault()}
    >
      <IconButton sx={{ p: '10px' }} aria-label="menu">
        <SearchIcon />
      </IconButton>
      <InputBase
        sx={{ ml: 1, flex: 1 }}
        placeholder="Search Google Maps"
        inputProps={{ 'aria-label': 'search google maps' }}
        value={props.text}
        onChange={(e) => props.setText(e.target.value)}
      />
    </Paper>
  );
}
